/// -----------------------------------------------------------------------------
/// This file contains very basic styles.
/// -----------------------------------------------------------------------------

body{
  background-color: $body-color;
  font-family: $primary-font;
  font-style: normal;
  font-weight: 300;
  font-size: $body-font-size;
  color: $text-color;
  font-size: 12pt;
  line-height: 1.25;

  #site-preloader {
      position: fixed;
      top: 0px;
      bottom: 0px;
      left: 0px;
      right: 0px;
      z-index: 2000;
      background-color: white;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      opacity: 1;
      transition: all 0.5s ease;

      img{
          -webkit-animation: imageOpacity 3s ease-in infinite;
          animation: imageOpacity 3s ease-in infinite;
      }
  }
}

h1,
h2,
h3,
h4,
h5{
  color: $primary-color;
  font-weight: bold;
  line-height: 1.3;
  text-transform: none;
}

/// Basic styles for text
p{
  color: $text-color;
  line-height: 1.25;
  word-wrap: break-word;
  font-weight: 300;
}

/// Basic styles for links
a{
  color: $hover-color;
  line-height: 1.3;
  text-decoration: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;

  @include on-event{
    color: $hover-color;
    text-decoration: underline;
  }
}

/// Text selection colors
::-moz-selection{
  background: $primary-color;
  color: $white;
}

::selection{
  background: $primary-color;
  color: $white;
}

